import React from 'react'
import './index.less'
import NoneData from 'components/NoneData/all-none'
import {withRouter} from 'react-router-dom'
import connect from 'connect'
import { List, TextareaItem, NavBar, ImagePicker, Card, Flex, Button } from 'antd-mobile';
const data = [];




@connect
@withRouter
class Home extends React.Component {
    constructor(props){
        super(props);
        
        this.state = {
          navTop: false,
          files: data,
          multiple: false,
        }

        this.$tab = null;
        this.offsetTop = 0;
    }

    
    componentDidMount () {
       setTimeout(() => {
             this.setState({
               data: ['AiyWuByWklrrUDlFignR', 'TekJlZRVCjLFexlOCuWn', 'IJOtIlfsYdTyaDTRVrLI'],
             });
           }, 100);
    }


    onChange = (files, type, index) => {
      console.log(files, type, index);
      this.setState({
        files,
      });
    }
    onSegChange = (e) => {
      const index = e.nativeEvent.selectedSegmentIndex;
      this.setState({
        multiple: index === 1,
      });
    }
    handleScroll(){
        let sTop = document.documentElement.scrollTop || window.pageYOffset || document.body.scrollTop;

        if(!this.state.navTop && sTop >= this.offsetTop){
           this.setState({
             navTop: true
           })
        }

        if(sTop < this.offsetTop){
           this.setState({
             navTop:false
           })
        }
      }

    pingfen() {
     alert('1')
    }

    renderTabBar(props) {
      return (<div>
        {({ style }) => <div style={{ ...style, zIndex: 1 }}><Tabs.DefaultTabBar {...props} /></div>}
      </div>);
    }

    onChange2 = (val) => {
        // console.log(val);
        this.setState({ val });
      }
      onChange1 = (val1) => {
        // console.log(val);
        this.setState({ val1 });
      }


    render () {
      
      const data22 = [
            { value: 0, label: 'Ph.D.' },
            { value: 1, label: 'Bachelor' },
            { value: 2, label: 'College diploma' },
          ];
        const Item = List.Item;
        const Brief = Item.Brief;
        const { files } = this.state;
       
        return (
            <div className="feedback-container">
              <NavBar
                   mode="light"
                   icon={<img src={require('../../images/back.png')} alt="LOGO" className="back"  onClick={this.props.history.goBack}/>}
                 >意见反馈</NavBar>
              <List>
                        <TextareaItem
                          rows={5}
                          placeholder='请输入您的意见或建议'
                        />
              </List>
              <div className="cardfooter">
                  <Button>提交</Button>
              </div>
             
             
            </div>
        )
    }
}
export default Home


